const baseUrl = 'http://206.189.67.119:8080/api/';

// const cloudServer = '123.235.4623.com:5000/api/'

const MyMapComponent = withScriptjs(withGoogleMap((props) => {
    return (<GoogleMap
                defaultZoom={10}
                ref={props.onMapMounted}
                zoom={props.zoom}
                onZoomChanged={props.onZoomChanged}
               defaultCenter={{
                   lng: props.stoneLocations.length > 0 ? props.stoneLocations[0].longitude : -123.1207,
                   lat: props.stoneLocations.length > 0 ? props.stoneLocations[0].latitude : 49.2827}}
            >
            {
                props.stoneLocations.map( (stone,ndx) => {
                    return (
                        <Marker
                            key={ndx}
                            position={{lng: stone.longitude, lat: stone.latitude}}
                            label={`${ndx}`}
                            onClick={()=>{props.onMarkerClick(stone)}}
                        />)
                })
            }
                {/*{props.isMarkerShown && <Marker icon={{*/}
                    {/*url: 'http://maps.google.com/mapfiles/ms/icons/blue-dot.png'*/}
                {/*}} position={{lng: -123.1207, lat: 49.2827}}/>}*/}
            </GoogleMap>
        )
    }
));

